import React from 'react';
import P from '../imagenes/P.jpg';
import P1 from '../imagenes/P1.jpeg';
const page4 = () => {
  return (
    <div>
      <h2>Cuarta Generación de Computadoras</h2>
      
      <p>La denominada cuarta generación es el producto del microprocesador de los circuitos electrónicos. El tamaño reducido de los chips hizo posible la creación de las computadoras personales (PC). Hoy en día las tecnologías LST (integración a gran escala) y VLSI (integración a Mayor escala) permiten que cientos de miles de componentes electrónicos se almacenen en un microchip. Usando VLs, un fabricante puede hacer que una computadora pequeña rivalice con una computadora de la primera generación que ocupaba un cuarto completo. Hicieron su gran debut las microcomputadoras.

Hizo que sea una computadora ideal para uso “personal”, de ahí que el término “PC” se estandarizara y los clones que sacaron posteriormente otras empresas fueron llamados “PC y compatibles”, usando procesadores del mismo tipo que las IBM , pero a un costo menor y pudiendo ejecutar el mismo tipo de programas. Existen otros tipos de microcomputadoras , como la Macintosh, que no son compatibles con la IBM, pero que en muchos de los casos se les llaman también “PC”, por ser de uso personal. El primer microprocesador fue el Intel 4004, producido en 1971. Se desarrolló originalmente para una calculadora, y resultaba revolucionario para su época. Contenía 2300 transistores en un microprocesador de 4 bits que solo podía realizar 60 000 operaciones por segundo.La computadora de cuarta generación es la más moderna, la que le sigue es la quinta generación.</p>
<div className="row">
        <div className="col-md-6 mb-3">
          <a href="https://es.wikipedia.org/wiki/Cuarta_generaci%C3%B3n_de_computadoras" target="_blank" rel="noopener noreferrer">
            <img
              src={P} 
              alt="Descripción de la primera imagen"
              className="img-fluid"
            />
            <p>Descripción de la primera imagen</p>
          </a>
        </div>
        <div className="col-md-6 mb-3">
          <a href="https://es.wikipedia.org/wiki/Cuarta_generaci%C3%B3n_de_computadoras" target="_blank" rel="noopener noreferrer">
            <img
              src={P1}  
              alt="Descripción de la segunda imagen"
              className="img-fluid"
            />
            <p>Descripción de la segunda imagen</p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default page4;