import React from 'react';
import ENIAC1 from '../imagenes/ENIAC1.jpg';
import ENIAC from '../imagenes/ENIAC.jpg';
const page6 = () => {
  return (
    <div>
      <h2>ENIAC</h2>
      <p>ENIAC, que significa Computadora e Integrador Numérico Electrónico, fue una de las primeras computadoras digitales electrónicas de uso general. Fue diseñado y construido durante la Segunda Guerra Mundial y entró en funcionamiento en 1945. ENIAC era una máquina enorme, que constaba de miles de tubos de vacío y kilómetros de cableado.

Datos clave sobre ENIAC:

Propósito: ENIAC fue construido para resolver cálculos numéricos complejos, particularmente aquellos necesarios para fines militares, como los cálculos de trayectoria de artillería.

Construcción y tamaño: ENIAC se construyó en la Escuela de Ingeniería Eléctrica Moore de la Universidad de Pensilvania. Era increíblemente grande y pesado, ocupaba una habitación de unos 30 por 50 pies y pesaba alrededor de 30 toneladas.

Componentes: ENIAC utilizó aproximadamente 17.468 tubos de vacío para el cálculo, junto con una gran cantidad de interruptores y cables. No tenía una unidad central de procesamiento (CPU) como la tienen las computadoras modernas; en cambio, se programó utilizando una combinación de enchufes e interruptores.

Velocidad y capacidades: ENIAC podía realizar alrededor de 5000 sumas por segundo y era capaz de resolver una amplia gama de problemas numéricos. Era órdenes de magnitud más rápido que los dispositivos informáticos mecánicos anteriores.

Programación: La programación de ENIAC fue un proceso manual y que requirió mucho tiempo. La programación implicaba configurar físicamente las conexiones mediante enchufes e interruptores, y podría llevar semanas reprogramar la máquina para una tarea diferente.

ENIAC jugó un papel crucial en el desarrollo de la tecnología informática, allanando el camino para las generaciones posteriores de computadoras. Su construcción marcó el comienzo de la era de la informática electrónica y demostró el potencial de las computadoras electrónicas para aplicaciones científicas y de ingeniería.</p>
<div className="row">
        <div className="col-md-6 mb-3">
          <a href="https://en.wikipedia.org/wiki/ENIAC" target="_blank" rel="noopener noreferrer">
            <img
              src={ENIAC1} 
              alt="Descripción de la primera imagen"
              className="img-fluid"
            />
            <p>Descripción de la primera imagen</p>
          </a>
        </div>
        <div className="col-md-6 mb-3">
          <a href="https://en.wikipedia.org/wiki/ENIAC" target="_blank" rel="noopener noreferrer">
            <img
              src={ENIAC}  
              alt="Descripción de la segunda imagen"
              className="img-fluid"
            />
            <p>Descripción de la segunda imagen</p>
          </a>
        </div>
      </div>
    </div>
  );
};

export default page6;